module.exports = "<h1 id=\"vue2-calendar\">vue2-calendar</h1>\n<blockquote>\n<p>vue 2 calendar, datepicker component which supported lunar or date event</p>\n</blockquote>\n<p align=\"center\">\n  <a href=\"http://blog.w3cub.com/vue2-calendar/?x-page=github\">\n    Live Demo &gt;&gt;\n  </a>\n  <br>\n  <br>\n  <a href=\"https://github.com/icai/vue2-calendar\">\n    <img src=\"https://img.shields.io/github/stars/icai/vue2-calendar.svg?style=social&label=Star\" alt=\"\">\n  </a>\n  <a href=\"https://github.com/icai/vue2-calendar\">\n    <img src=\"https://img.shields.io/github/forks/icai/vue2-calendar.svg?style=social&label=Fork\" alt=\"\">\n  </a>\n  <a href=\"https://github.com/icai/vue2-calendar\">\n    <img src=\"https://img.shields.io/github/watchers/icai/vue2-calendar.svg?style=social&label=Watch\" alt=\"\">\n  </a>\n  <br>\n  <br>\n  <a href=\"https://github.com/icai/vue2-calendar/issues\">\n    <img src=\"https://img.shields.io/github/issues/icai/vue2-calendar.svg?style=flat-square\" alt=\"\">\n  </a>\n  <a href=\"https://github.com/icai/vue2-calendar/issues\">\n    <img src=\"http://isitmaintained.com/badge/resolution/icai/vue2-calendar.svg?style=flat-square\" alt=\"\">\n  </a>\n  <a href=\"https://github.com/icai/vue2-calendar/graphs/contributors\">\n    <img src=\"https://img.shields.io/github/contributors/icai/vue2-calendar.svg?style=flat-square\" alt=\"\">\n  </a>\n  <br>\n  <a href=\"https://www.npmjs.com/package/vue2-slot-calendar\">\n    <img src=\"https://img.shields.io/npm/l/vue2-slot-calendar.svg?style=flat-square\" alt=\"\">\n  </a>\n  <a href=\"https://www.npmjs.com/package/vue2-slot-calendar\">\n    <img src=\"https://img.shields.io/npm/v/vue2-slot-calendar.svg?style=flat-square\" alt=\"\">\n  </a>\n  <a href=\"https://www.npmjs.com/package/vue2-slot-calendar\">\n    <img src=\"https://img.shields.io/npm/dm/vue2-slot-calendar.svg?style=flat-square\" alt=\"\">\n  </a>\n  <a href=\"https://www.npmjs.com/package/vue2-slot-calendar\">\n    <img src=\"https://img.shields.io/npm/dt/vue2-slot-calendar.svg?style=flat-square\" alt=\"\">\n  </a>\n  <br>\n  <br>\n</p>\n\n\n<ul>\n<li>This project is not only a vue component, but also a webpack <strong>multi-page</strong> project in action.</li>\n</ul>\n<h2 id=\"install\">Install</h2>\n<pre><code class=\"lang-bash\">$ npm install vue2-slot-calendar\n</code></pre>\n<h3 id=\"import-using-module\">Import using module</h3>\n<pre><code class=\"lang-JavaScript\">\nimport &#39;bootstrap-sass&#39; // alias css\n\n// js file\nimport &#39;vue2-slot-calendar/lib/calendar.min.css&#39;;\nimport calendar from &#39;vue2-slot-calendar/lib/calendar&#39;;\n\n// vue file\n// in ES6 modules\nimport Calendar from &#39;vue2-slot-calendar&#39;;\n\n// in CommonJS\nconst Calendar = require(&#39;vue2-slot-calendar&#39;);\n\n// in Global variable\nconst VueCalendar = Calendar;\n</code></pre>\n<h3 id=\"import-using-script-tag\">Import using script tag</h3>\n<pre><code class=\"lang-HTML\">&lt;link rel=&quot;stylesheet&quot; href=&quot;https://maxcdn.bootstrapcdn.com/bootstrap/3.3.7/css/bootstrap.min.css&quot;&gt;\n&lt;link rel=&quot;stylesheet&quot; href=&quot;../node_modules/vue2-slot-calendar/lib/calendar.min.css&quot; &gt;\n&lt;script src=&quot;../node_modules/vue2-slot-calendar/lib/calendar.min.js&quot;&gt;&lt;/script&gt;\n</code></pre>\n<p>Also see the demo file, <code>example/demo.html</code></p>\n<h2 id=\"build-setup\">Build Setup</h2>\n<pre><code class=\"lang-bash\"># install dependencies\nnpm install\n\n# serve with hot reload at localhost:4000\nnpm run dev\n\n# build for production with minification\nnpm run build\n\n# run unit tests\nnpm run unit\n\n# run all tests\nnpm test\n</code></pre>\n<p>Chinese user: <a href=\"https://npm.taobao.org/\"><img src=\"https://npm.taobao.org/badge/v/cnpmjs.org.svg\" alt=\"cnpm\"></a></p>\n<h2 id=\"screenshot\">Screenshot</h2>\n<p><img src=\"https://raw.githubusercontent.com/icai/vue2-calendar/master/src/assets/demo.gif?1477232397\" alt=\"\"></p>\n<h2 id=\"usage\">Usage</h2>\n<pre><code class=\"lang-html\">  &lt;calendar\n    :value=&quot;value&quot;\n    :disabled-days-of-week=&quot;disabled&quot;\n    :format=&quot;format&quot;\n    :clear-button=&quot;clear&quot;\n    :placeholder=&quot;placeholder&quot;\n    :pane=&quot;2&quot;\n    :has-input=&quot;false&quot;\n    :on-day-click=&quot;onDayClick2&quot;\n    :special-days=&quot;_dateMap&quot;\n  &gt;&lt;/calendar&gt;\n</code></pre>\n<h3 id=\"use-slot-to-render-async-data\">Use slot to render async data</h3>\n<pre><code class=\"lang-html\">\n  &lt;calendar class=&quot;event-calendar&quot; :value=&quot;value&quot; :disabled-days-of-week=&quot;disabled&quot; :format=&quot;format&quot; :clear-button=&quot;clear&quot; :placeholder=&quot;placeholder&quot; :pane=&quot;2&quot; :has-input=&quot;false&quot; :on-day-click=&quot;onDayClick3&quot; :change-pane=&quot;changePane&quot;&gt;\n    &lt;div v-for=&quot;evt in events&quot; :slot=&quot;evt.date&quot;&gt;\n        ${{evt.content}} &lt;i :class=&quot;{low : evt.low}&quot; v-if=&quot;evt.low&quot;&gt;&lt;/i&gt;\n    &lt;/div&gt;\n  &lt;/calendar&gt;\n</code></pre>\n<h3 id=\"range-hover-status\">Range Hover Status</h3>\n<pre><code class=\"lang-html\">\n  &lt;calendar :value=&quot;value&quot; :disabled-days-of-week=&quot;disabled&quot; :format=&quot;format&quot;\n   :clear-button=&quot;clear&quot; :placeholder=&quot;placeholder&quot; :pane=&quot;2&quot;\n   :range-bus=&quot;getBus&quot; :range-status=&quot;1&quot;&gt;&lt;/calendar&gt;\n\n  &lt;calendar :value=&quot;value&quot; :disabled-days-of-week=&quot;disabled&quot; :format=&quot;format&quot;\n   :clear-button=&quot;clear&quot; :placeholder=&quot;placeholder&quot; :pane=&quot;2&quot;\n   :range-bus=&quot;getBus&quot; :range-status=&quot;2&quot;&gt;&lt;/calendar&gt;\n</code></pre>\n<h3 id=\"props\">Props</h3>\n<p>Options/Props</p>\n<table>\n<thead>\n<tr>\n<th>Name</th>\n<th>Type</th>\n<th>Default</th>\n<th>Description</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>value</td>\n<td><code>String</code></td>\n<td>&#39;&#39;</td>\n<td>Value of the input DOM</td>\n</tr>\n<tr>\n<td>width</td>\n<td><code>String</code></td>\n<td>&#39;200px&#39;</td>\n<td>Width of the input DOM</td>\n</tr>\n<tr>\n<td>format</td>\n<td><code>String</code></td>\n<td><code>MMMM/dd/yyyy</code></td>\n<td>The date format, combination of d, dd, M, MM, MMM, MMMM, yyyy.</td>\n</tr>\n<tr>\n<td>disabled-days-of-week</td>\n<td><code>Array</code></td>\n<td></td>\n<td>Days of the week that should be disabled. Values are 0 (Sunday) to 6 (Saturday). Multiple values should be comma-separated.</td>\n</tr>\n<tr>\n<td>clear-button</td>\n<td><code>Bollean</code></td>\n<td>false</td>\n<td>If true shows an  shaped button to clear the selected date. Usefull in forms where date entry is optional.</td>\n</tr>\n<tr>\n<td>placeholder</td>\n<td><code>String</code></td>\n<td></td>\n<td>Placeholder to put on the input field when no date (null or empty) is set</td>\n</tr>\n<tr>\n<td>hasInput</td>\n<td><code>Boolean</code></td>\n<td>true</td>\n<td>Default is has-input style, if don&#39;t have input will show pane directly</td>\n</tr>\n<tr>\n<td>pane</td>\n<td><code>Number</code></td>\n<td>1</td>\n<td>pane count</td>\n</tr>\n<tr>\n<td>borderWidth</td>\n<td><code>Number</code></td>\n<td>2</td>\n<td>This value is for calculating the pane width</td>\n</tr>\n<tr>\n<td>onDayClick</td>\n<td><code>Function</code></td>\n<td></td>\n<td>Only for <code>hasInput</code> set false</td>\n</tr>\n<tr>\n<td>specialDays</td>\n<td><code>Object</code></td>\n<td></td>\n<td>To repalce the day text</td>\n</tr>\n<tr>\n<td>changePane</td>\n<td><code>Function</code></td>\n<td></td>\n<td>For pane change parameter (year, month, pane) month[0,11], demo <code>/src/modules/Docs.vue</code></td>\n</tr>\n<tr>\n<td>rangeBus</td>\n<td><code>Function</code></td>\n<td></td>\n<td>should return <code>new Vue()</code> as sibling component communication events bus</td>\n</tr>\n<tr>\n<td>rangeStatus</td>\n<td><code>Number</code></td>\n<td>0</td>\n<td>Default is <code>disabled</code> range hover effect,  currently only support [0,1,2] <code>1</code> will communicate with <code>2</code></td>\n</tr>\n<tr>\n<td>onDrawDate</td>\n<td><code>Function</code></td>\n<td></td>\n<td>DrawDate Function  <code>allowSelect</code> to update date cell style</td>\n</tr>\n<tr>\n<td>showDateOnly</td>\n<td><code>Boolean</code></td>\n<td>false</td>\n<td>show date pane only</td>\n</tr>\n<tr>\n<td>transfer</td>\n<td><code>Boolean</code></td>\n<td>false</td>\n<td>transfer popup to <code>document.body</code></td>\n</tr>\n<tr>\n<td>elementId</td>\n<td><code>String</code></td>\n<td></td>\n<td>elementId for label tag <code>for</code> attribute</td>\n</tr>\n</tbody>\n</table>\n<h3 id=\"events\">Events</h3>\n<table>\n<thead>\n<tr>\n<th>Name</th>\n<th>Description</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>drawdate</td>\n<td>drawdate Event like  <code>onDrawDate</code></td>\n</tr>\n</tbody>\n</table>\n<pre><code class=\"lang-javascript\">  props: {\n    value: {\n      type: String\n    },\n    format: {\n      default: &#39;MM/dd/yyyy&#39;\n    },\n    disabledDaysOfWeek: {\n      type: Array,\n      default () {\n        return []\n      }\n    },\n    width: {\n      type: String,\n      default: &#39;200px&#39;\n    },\n    clearButton: {\n      type: Boolean,\n      default: false\n    },\n    lang: {\n      type: String,\n      default: navigator.language\n    },\n    placeholder: {\n      type: String\n    },\n    hasInput: {\n      type: Boolean,\n      default: true\n    },\n    pane: {\n      type: Number,\n      default: 1\n    },\n    borderWidth: {\n      type: Number,\n      default: 2\n    },\n    onDayClick: {\n      type: Function,\n      default () {}\n    },\n    changePane: {\n      type: Function,\n      default () {}\n    },\n    specialDays: {\n      type: Object,\n      default () {\n        return {}\n      }\n    },\n    rangeBus: {\n      type: Function,\n      default () {\n        // return new Vue()\n      }\n    },\n    rangeStatus: {\n      type: Number,\n      default: 0\n    },\n    onDrawDate: {\n      type: Function,\n      default () {}\n    },\n    showDateOnly: {\n      type: Boolean,\n      default: false\n    },\n    transfer: {\n      type: Boolean,\n      default: false\n    },\n    elementId: [String]\n\n  }\n</code></pre>\n<h2 id=\"credits\">Credits</h2>\n<p>Inspired by <a href=\"https://github.com/yuche/vue-strap\">vue-strap</a> datepicker component.</p>\n<p>For detailed explanation on how things work, checkout the <a href=\"http://vuejs-templates.github.io/webpack/\">guide</a> and <a href=\"http://vuejs.github.io/vue-loader\">docs for vue-loader</a>.</p>\n";

